import React from 'react'
import './Style.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import data from '../data/productData';
import { SetPrice } from '../component/SetPrice';
import { useParams } from 'react-router-dom'
import { addItem } from './store';
import { useDispatch, useSelector } from 'react-redux'; 
import { useState } from 'react';
import { Button } from 'react-bootstrap';

export default function Detail() {
  
  const [best] = useState(data);
  const {id} = useParams();
  const state = useSelector((state) => state);
  const dispatch = useDispatch();
  const price = best[id].price;
  const [count, setCount] = useState(1);

  return (
    <>
    <div className="infoWrap">
      <div className="infoBox">
        <img className="deImg" src={best[id].image} alt="bestProduct"/>
        <div className="productInfo kor">
          <h6 className="deEngTit engA">{best[id].eng}</h6>
          <h3 className="deTitle">{best[id].title}</h3>
          <h6 className="deDesc">{best[id].desc}</h6>
          <p className="dePrice">{SetPrice(price * count)}원</p>

        <div className="detailCount">
          <button onClick={() => {
            if (count === 1) {
              return 1;
            }
            setCount(count - 1);
            }}>
              <i class="fas fa-minus"></i>
          </button>
          <p>{count}</p>
          <button onClick={() => {
            setCount(count + 1);
          }}>
            <i class="fas fa-plus"></i>
          </button>
        </div>
        <div className="line"></div>
      
        <Button className="cartBtn" onClick={() => { 
          dispatch(
            addItem({
              id: best[id].id, 
              image: best[id].image,
              title: best[id].title, 
              price: best[id].price, 
              count: count
            }))
          }}>장바구니</Button>
        </div>
      </div>
    </div>

    <div className="detailImg">
      <img src={best[id].detail} alt="bestProduct_detail"></img>
    </div>
    </>
  )
}
