import React from 'react';
import "./Style.css";
import { BsCart2 } from 'react-icons/bs';
import { SetPrice } from '../component/SetPrice';
import { Table, Button } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux'; 
import { deleteItem, addCount, subCount } from './store';

export default function Cart() {
  const state = useSelector((state) => state)
  const dispatch = useDispatch()

  let totalPrice = 0;
  state.cart.map((item, i) => {
    totalPrice += item.price * item.count;
  });

  let shippingFee = 0;
  state.cart.map((item, i) => {
    if(totalPrice < 30000) {
      return (shippingFee = 2500)
    } else {
      return (shippingFee = 0);
    }
  });
  
  
  return (
    <div className='cart'>
      <h1 className='cartTit engB'>CART</h1>
      <div className='cartBox'>
        <div className='product'>
          <h3 className='engB'>Product</h3>
          <div className='line'></div>

          {state.cart.length > 0 ? (
            <Table className='cartTable'>  
              <tbody className='tbody kor'>
                {
                  state.cart.map((item, i) => {
                    return (
                    <tr className='tBox' style={{width: 100, height: "auto"}} key={i}>
                      <td className='proImg'>
                        <img src={state.cart[i].image} alt="product visual" style={{width: 100}}/>
                      </td>
                      <td className='cartTitle'>{state.cart[i].title}</td>
                      <td className='cartPrice'>{SetPrice(state.cart[i].price * state.cart[i].count)}원</td>
                      <td className='count'>
                        <button onClick={() => dispatch(subCount(state.cart[i].id))}><i class="fas fa-minus"></i></button>
                        <div className='num'>{state.cart[i].count}</div>
                        <button onClick={() => dispatch(addCount(state.cart[i].id))}> <i class="fas fa-plus"></i></button>
                      </td>
                      <td className='delete'>
                        <button className='deleteBtn' onClick={() => dispatch(deleteItem(state.cart[i].id))}><i class="fas fa-times"></i></button>
                      </td>
                    </tr>
                  )
                })
              }
            </tbody>
          </Table>
          ) : (
            <div className='empty kor'>
              <BsCart2 className='emptyIcon' size="30" title="cart" color="#123128" />
              <p className='emptyCart'>장바구니가 비어 있습니다.</p>
            </div>
        )}
          <div className='line'></div>
        </div>

        <div className='total kor'>
          <h3 className='engB'>Total</h3>
          <div className='line'></div>
          <div className='totalList'>
            <div className='orderPrice align'>
              <p className='title'>주문금액</p>
              <p className='price'>{totalPrice.toLocaleString()}원</p>
            </div>

            <div className='shipping align'>
              <p className='title'>배송비</p>
              <p className='price'>{SetPrice(shippingFee.toLocaleString())}원</p>
            </div>
          </div>

          <div className='line'></div>

          <div className='shippingNotice'>
            <p className='shippingPrice kor'>* 30,000원 이상 구매시 무료배송 혜택</p>
          </div>

          <div className='line'></div>

          <div className='totalPrice align'>
            <p className='title'>합계</p>
            <p className='price'>{SetPrice(totalPrice + shippingFee)}원</p>
          </div>
          <Button className='buyBtn'>구매하기</Button>
        </div>
      </div>
    </div>   
  )
}


