import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './Style.css';
import NewData from '../data/NewData';
import NewDetail from './NewDetail';
import { Container, Row, Col, Button } from 'react-bootstrap';
import { Routes, Route, Link } from 'react-router-dom';
import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { addItem } from './store';

export default function Best() {
  const dispatch = useDispatch()
  const [newPro] = useState(NewData)
  const state = useSelector((state) => state)
  return (
    <>
    <Routes>
    <Container style={{width: "100vw"}}>
            <img src={process.env.PUBLIC_URL + '/images/shop_013.png'} alt="main visual" style={{width: "100%"}}/>
            <h1 style={{padding: 30}}>BEST</h1>
            <Row>
          {
            newPro.map((newPro, index) => {
              return (
                <Col key={index} style={{width: 626}}>
                  <Link to={`detail/${index}`}>
                    <img src={newPro.image} alt="product_img" style={{width: 600}}/>
                    <h5 className="kor" style={{padding: "25px 0 1px"}}>{newPro.title}</h5>
                    <p className="kor" style={{fontSize: 16, fontWeight: "bold", color: "#4a6a53"}}>{newPro.desc}</p>
                    <p className="kor" style={{fontSize: 22, color: "#3d7c69"}}>{newPro.price}</p>
                  </Link>
                  <Button className="cartBtn" onClick={() => { 
                    dispatch(
                      addItem({
                        id: newPro.id, 
                        image: newPro.image,
                        title: newPro.title, 
                        price: newPro.price, 
                        count: 1
                      }))
                  }} style={{marginBottom: 30}}>장바구니</Button>{' '}
                </Col>
              )
            })
          }
        </Row>
        </Container>
        <Route path='detail/:id' element={<NewDetail newPro={NewData}/>}></Route>
        </Routes>
    </>
  )
}
