import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import ZeroData from '../data/ZeroData';
import ZeroDetail from './ZeroDetail';
import { Container, Row, Col, Button } from 'react-bootstrap';
import { Routes, Route, Link } from 'react-router-dom';
import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { addItem } from './store';

export default function Best() {
  const dispatch = useDispatch()
  const [zero] = useState(ZeroData)
  const state = useSelector((state) => state)
  return (
    <>
    <Routes>
    <Container style={{width: "100vw"}}>
            <img src={process.env.PUBLIC_URL + '/images/shop_013.png'} alt="main visual" style={{width: "100%"}}/>
            <h1 style={{padding: 30}}>BEST</h1>
          <Row>
            {
              zero.map((zero, index) => {
                return (
                  <Col key={index}>
                    <Link to={`ZeroDetail/${index}`}>
                      <img src={zero.image} alt="product_img" style={{width: 400}}/>
                      <h6 style={{padding: 10}}>{zero.title}</h6>
                      <p style={{fontSize: 12}}>{zero.desc}</p>
                      <p>{zero.price}</p>
                    </Link>
                    <Button className="cartBtn" onClick={() => { 
                      dispatch(addItem({id: zero.id, title: zero.title, count: 1}))
                    }} style={{marginBottom: 30}}>장바구니</Button>{' '}
                  </Col>
                )
              })
            }
          </Row>
        </Container>
        <Route path='detail/:id' element={<ZeroDetail zero={ZeroData}/>}></Route>
      </Routes>
    </>
  )
}
