import React from 'react';
import './Style.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import data from '../data/productData';
import { SetPrice } from '../component/SetPrice';
import Detail from './Detail';
import NewData from '../data/NewData';
import NewDetail from './NewDetail';
import ZeroData from '../data/ZeroData';
import ZeroDetail from './ZeroDetail';
import { Tab , Tabs, Row, Col, Button } from 'react-bootstrap';
import { Routes, Route, Link, useNavigate } from 'react-router-dom';
import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { addItem } from './store';

export default function Shop() {
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const [best] = useState(data)
  const [newPro] = useState(NewData)
  const [zero] = useState(ZeroData)
  const state = useSelector((state) => state)

  return (
    <>
    <Routes>
      <Route className="shop" path="/" element={
        <>
        <img className="shopMainImg" src={process.env.PUBLIC_URL + "/images/banner_03.jpg"} alt="main visual"/>
        <Tabs
        defaultActiveKey="BEST"
        id="fill-tab-example"
        className="tabBox engC"
        >
        <Tab className="tabTit best" eventKey="BEST" title="BEST">
        <Row>
          {
            best.map((best, index) => {
              return (
                <Col className="shopColumn" key={index}>
                  <Link to={`detail/${index}`}>
                    <img className="shopImg" src={best.image} alt="product_img" style={{width: 600}}/>
                    <h5 className="shopTit kor">{best.title}</h5>
                    <p className="shopDesc kor">{best.desc}</p>
                    <p className="shopPrice kor">{SetPrice(best.price)}원</p>
                  </Link>
                  <Button className="cartBtn" onClick={() => { 
                    dispatch(
                      addItem({
                        id: best.id, 
                        image: best.image,
                        title: best.title, 
                        price: best.price, 
                        count: 1
                      }))
                  }}>장바구니</Button>
                </Col>
              )
            })
          }
        </Row>
        </Tab>
        
        <Tab className="tabTit newPro" eventKey="NEW" title="NEW">
        <Row>
          {
            newPro.map((newPro, index) => {
              return (
                <Col className="shopColumn" key={index}>
                  <Link to={`NewDetail/${index}`}>
                    <img className="shopImg" src={newPro.image} alt="product_img"/>
                    <h5 className="shopTit kor">{newPro.title}</h5>
                    <p className="shopDesc kor">{newPro.desc}</p>
                    <p className="shopPrice kor">{SetPrice(newPro.price)}원</p>
                  </Link>
                  <Button className="cartBtn" onClick={() => { 
                    dispatch(
                      addItem({
                        id: newPro.id, 
                        image: newPro.image,
                        title: newPro.title, 
                        price: newPro.price, 
                        count: 1
                      }))
                  }}>장바구니</Button>
                </Col>
              )
            })
          }
        </Row>
        </Tab>
        
        <Tab className="tabTit zero" eventKey="ZERO WASTE" title="ZERO WASTE">
        <Row>
          {
            zero.map((zero, index) => {
              return (
                <Col className="shopColumn" key={index}>
                  <Link to={`ZeroDetail/${index}`}>
                    <img className="shopImg" src={zero.image} alt="product_img"/>
                    <h5 className="shopTit kor">{zero.title}</h5>
                    <p className="shopDesc kor">{zero.desc}</p>
                    <p className="shopPrice kor">{SetPrice(zero.price)}원</p>
                  </Link>
                  <Button className="cartBtn" onClick={() => { 
                    dispatch(
                      addItem({
                        id: zero.id, 
                        image: zero.image,
                        title: zero.title, 
                        price: zero.price, 
                        count: 1
                      }))
                  }}>장바구니</Button>
                </Col>
              )
            })
          }
        </Row>
        </Tab>
      </Tabs>
      </>
      }/>
       <Route path="Detail/:id" element={<Detail best={best}/>}/>
       <Route path="NewDetail/:id" element={<NewDetail newPro={newPro}/>}/>
       <Route path="ZeroDetail/:id" element={<ZeroDetail zero={zero}/>}/>
      </Routes>
    </>
  );
}
