import React from 'react';
import "./Style.css";
import { BsCart2 } from 'react-icons/bs';
import 'bootstrap/dist/css/bootstrap.min.css';
import { useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { Container, Nav, Navbar } from 'react-bootstrap';

export default function Header () {
  const state = useSelector((state) => state)
  const navigate = useNavigate()

  return (
    <header className="header">
      <Navbar className="navBar">
        <Container className="engA">
          <Navbar.Brand className="navBrand" onClick={() => {navigate('/')}}>AROMATICA</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link className="brand navMenu" onClick={() => {navigate('/brand')}}>BRAND</Nav.Link>
            <Nav.Link className="circle navMenu" onClick={() => {navigate('/circle')}}>JOIN THE CIRCLE</Nav.Link>
            <Nav.Link className="shop navMenu" onClick={() => {navigate('/shop')}}>SHOP</Nav.Link>
          </Nav>

          <Nav className="navCart">
            <Nav.Link className="cart" onClick={() => {navigate('/cart')}}>
              <BsCart2 size="30" title="cart" />
              {state.cart.length > 0 ? <p className='cartCount kor'>{state.cart.length}</p> : null}
            </Nav.Link>
          </Nav>
        </Container>
      </Navbar>
    </header>
  )
}

