import React from 'react'
import './Style.css';
import ZeroData from '../data/ZeroData';
import { SetPrice } from '../component/SetPrice';
import 'bootstrap/dist/css/bootstrap.min.css';
import { useParams } from 'react-router-dom'
import { addItem } from './store'
import { addCount, subCount } from './store';
import { useDispatch, useSelector } from 'react-redux'; 
import { useState } from 'react';
import { Button } from 'react-bootstrap';

export default function NewDetail() {

  const [zero] = useState(ZeroData)
  const {id} = useParams()
  const state = useSelector((state) => state)
  const dispatch = useDispatch()
  const price = zero[id].price;
  const [count, setCount] = useState(1);

  return (
    <>
    <div className="infoWrap">
      <div className="infoBox">
        <img className="deImg"  src={zero[id].image} alt="newProduct"></img>
        <div className="productInfo kor">
          <h6 className="deEngTit engA">{zero[id].eng}</h6>
          <h3 className="deTitle">{zero[id].title}</h3>
          <h6 className="deDesc">{zero[id].desc}</h6>
          <p className="dePrice">{SetPrice(price * count)}원</p>

        <div className="detailCount">
          <button onClick={() => {
            if (count === 1) {
              return 1;
            }
            setCount(count - 1);
            }}>
              <i class="fas fa-minus"></i>
          </button>
          <p>{count}</p>
          <button onClick={() => {
            setCount(count + 1);
          }}>
            <i class="fas fa-plus"></i>
          </button>
        </div>
        <div className="line"></div>
      
        <Button className="cartBtn" onClick={() => { 
          dispatch(
            addItem({
              id: zero[id].id, 
              image: zero[id].image,
              title: zero[id].title, 
              price: zero[id].price, 
              count: count
            }))
          }}>장바구니</Button>
        </div>
      </div>
    </div>

    <div className="detailImg">
      <img src={zero[id].detail} alt="zeroProduct_detail"></img>
    </div>
    </>
  )
}
