import React from 'react'
import './Style.css';
import NewData from '../data/NewData';
import { SetPrice } from '../component/SetPrice';
import "bootstrap/dist/css/bootstrap.min.css";
import { useParams } from 'react-router-dom'
import { addItem } from './store'
import { addCount, subCount } from './store';
import { useDispatch, useSelector } from 'react-redux'; 
import { useState } from 'react';
import { Button } from 'react-bootstrap';

export default function NewDetail() {
  
  const [newPro] = useState(NewData)
  const {id} = useParams()
  const state = useSelector((state) => state)
  const dispatch = useDispatch()
  const price = newPro[id].price;
  const [count, setCount] = useState(1);

  return (
    <>
    <div className="infoWrap">
      <div className="infoBox">
        <img className="deImg" src={newPro[id].image} alt="newProduct"></img>
        <div className="productInfo kor">
          <h6 className="deEngTit engA">{newPro[id].eng}</h6>
          <h3 className="deTitle">{newPro[id].title}</h3>
          <h6 className="deDesc">{newPro[id].desc}</h6>
          <p className="dePrice">{SetPrice(price * count)}원</p>

        <div className="detailCount">
          <button onClick={() => {
            if (count === 1) {
              return 1;
            }
            setCount(count - 1);
            }}>
              <i class="fas fa-minus"></i>
          </button>
          <p>{count}</p>
          <button onClick={() => {
            setCount(count + 1);
          }}>
            <i class="fas fa-plus"></i>
          </button>
        </div>
        <div className="line"></div>
      
        <Button className="cartBtn" onClick={() => { 
          dispatch(
            addItem({
              id: newPro[id].id, 
              image: newPro[id].image,
              title: newPro[id].title, 
              price: newPro[id].price, 
              count: count
            }))
          }}>장바구니</Button>
        </div>
      </div>
    </div>

    <div className="detailImg">
      <img src={newPro[id].detail} alt="newProduct_detail"></img>
    </div>
    </>
  )
}
